@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

$light-color: #fff;
$accent-color: #65e4a6;
$bg-light-color: #f5f6fa;
$bg-dark-color: #44444f;
$gray-text: #696980;

body {
    font-family: "Roboto Slab", serif;
    font-size: 15px;
    line-height: 24px;
}

h1, h2, h3, h4, p {
    margin: 0;
    padding: 0;
}

h3 {
    font-size: 24px;
}

.wrap {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}

.page-main {
    background-image: url("../img/bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    color: $light-color;
    margin-bottom: 50px;
    padding-bottom: 163px;
}

.page {
    padding-top: 113px;
    padding-bottom: 160px;
    position: relative;

    &__title {
        font-size: 48px;
        text-align: center;
        color: $bg-dark-color;
        margin-top: 41px;
        margin-bottom: 25px;
        letter-spacing: 2.4px;
    }

    &__subtitle {
        color: $accent-color;
        text-align: center;
        font-size: 18px;
        letter-spacing: 1px;
    }
    &__svg {
        position: absolute;
        z-index: -1;
        top: 0;
        width: 100%;
        min-height: 694px;
        @media(max-width: 1020px) {
            display: none;
        }
    }

    &_gray {
        background-color: $bg-light-color;
        padding-bottom: 251px;
    }
    &__slider {
        margin-top: 170px;
        margin-bottom: 100px;
        @media (max-width: 1020px) {
            background-color: $bg-dark-color;
        }
    }
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1170px;
    margin: 0 auto;
    padding: 53px 17px 172px 15px;
    @media (max-width: 920px) {
        flex-direction: column;
    }
    @media (max-width: 750px) {
        flex-direction: row;
    }

    &__logo {
        display: block;
        width: 138px;
        height: 28px;
        background-image: url("../img/svg/logo.svg");
        background-repeat: no-repeat;
    }
    &__logo-burger {
        display: none;
        @media (max-width: 750px) {
            display: block;
            width: 30px;
            height: 30px;
            background-image: url("../img/svg/burger.svg");
            background-repeat: no-repeat;
        }
    }

    &-list {
        list-style-type: none;
        display: flex;
        justify-content: flex-end;
        font-size: 16px;
        font-family: "Open Sans", serif;
        @media (max-width: 750px) {
            display: none;
        }

        &__item {
            margin-left: 22px;
            padding-bottom: 7px;
            border-bottom: 3px solid transparent;
            transition: 0.3s;

            &:hover {
                border-bottom: 3px solid $accent-color;
            }
        }

        &__item.active {
            border-bottom: 3px solid $accent-color;
        }

        &__link {
            color: $light-color;
            text-decoration: none;
            letter-spacing: 0.5px;
        }
    }
}

.block-text {
    flex-basis: calc(93% - 500px);
    &-author {
        flex-basis: calc(93% - 517px);
        @media (max-width: 1020px) {
            flex-basis: calc(93% - 316px);
        }
    }

    &__name {
        font-family: "Open Sans", serif;
        font-size: 18px;
        letter-spacing: 0.7px;
        margin-bottom: 34px;
    }
    &__name_width {
        font-family: "Open Sans", serif;
        font-size: 18px;
        letter-spacing: 0.7px;
        margin-bottom: 20px;
    }

    &__name_top {
        margin: 6px 0 21px 0;
    }


    &__title {
        font-size: 48px;
        color: $light-color;
        font-weight: 700;
        letter-spacing: 1.6px;
        line-height: 50px;
        margin-bottom: 38px;

        span {
            display: block;
            color: $accent-color;
            font-size: 18px;
            margin-top: 12px;
            letter-spacing: 1px;
            line-height: 24px;
        }
    }
    &__title_width {
        color: $bg-dark-color;
        letter-spacing: 1.3px;
        margin-bottom: 40px;
    }
    &__title_author {
        font-size: 48px;
        color: $bg-dark-color;
        font-weight: 700;
        letter-spacing: 1.3px;
        margin-bottom: 40px;
    }

    &__description {
        font-family: "Open Sans", serif;
        font-size: 15px;
        line-height: 24px;
        letter-spacing: 0.75px;
        margin-bottom: 27px;
    }

    &__btn {
        display: block;
        background-color: $accent-color;
        color: $light-color;
        max-width: 246px;
        text-align: center;
        border-radius: 3px;
        font-size: 18px;
        text-decoration: none;
        margin-top: 45px;
        padding: 12px 0;
        letter-spacing: 0.7px;
        margin-bottom: 19px;
    }
    &--width {
        flex-basis: calc(93% - 517px);
        @media (max-width: 1020px) {
            flex-basis: calc(93% - 315px);
        }
    }

    &-item {
        margin-bottom: 56px;
        &__date {
            font-size: 15px;
            font-family: "Open Sans", serif;
            letter-spacing: 0.5px;
            margin-bottom: 4px;
        }

        &__title {
            font-size: 18px;
            font-family: "Roboto Slab", serif;
            letter-spacing: 0.9px;
            margin-bottom: 16px;
        }

        &__desc {
            font-size: 15px;
            font-family: "Open Sans", serif;
            letter-spacing: 0.75px;
        }
    }

    &-item__btn {
        display: block;
        background-color: $accent-color;
        color: $light-color;
        max-width: 138px;
        text-align: center;
        border-radius: 3px;
        font-size: 18px;
        text-decoration: none;
        margin-top: 24px;
        padding: 12px 0;
        letter-spacing: 0.7px;
        margin-bottom: 19px;
    }
}

small {
    font-style: italic;
}

.block-book {
    display: flex;
    justify-content: flex-start;

    &-demo {
        display: block;
        flex-basis: 500px;
        position: relative;
        width: 447px;
        height: 641px;
        background-image: url("../img/svg/book.svg");
        background-repeat: no-repeat;

        &__title {
            font-size: 36px;
            text-transform: uppercase;
            position: absolute;
            top: 123px;
            left: 132px;
            letter-spacing: 1.8px;

            span {
                display: block;
                font-family: "Open Sans", serif;
                font-size: 14px;
                color: $accent-color;
                text-transform: initial;
                font-weight: 100;
                letter-spacing: 0.6px;
                padding-top: 10px;
            }
        }

        &__author {
            position: absolute;
            font-family: "Open Sans", serif;
            font-size: 14px;
            color: $accent-color;
            bottom: 55px;
            left: 82px;
            letter-spacing: 1px;
        }
    }
}

.block-author {
    display: flex;
    justify-content: space-between;

    &__btn {
        display: block;
        max-width: 233px;
        padding: 12px 0;
        background-color: $accent-color;
        color: $light-color;
        text-align: center;
        border-radius: 3px;
        font-size: 18px;
        text-decoration: none;
        margin-top: 45px;
        letter-spacing: 0.7px;
        margin-bottom: 19px;
    }
}

.block-ava {
    position: relative;

    &__svg {
        position: absolute;
        z-index: -1;
        right: 0;
        @media (max-width: 1020px) {
            width: 300px;
            height: 412px;
        }
    }

    &__svg--left {
        right: inherit;
        left: 0;
    }

    &__img {
        margin-top: 41px;
        margin-right: 44px;
        @media (max-width: 1020px) {
            width: 300px;
        }
    }

    &__img--left {
        margin-top: 48px;
        margin-left: 51px;
        margin-right: 0;
    }
}

.block-counter {
    display: flex;
    justify-content: space-around;
    margin-top: 211px;
    @media (max-width: 750px) {
        flex-direction: column;
    }

    p {
        font-family: "Open Sans", serif;
        text-align: center;
        font-size: 18px;
        color: #696980;
        @media (max-width: 750px) {
            margin-bottom: 80px;
        }

        span {
            font-family: "Roboto Slab", serif;
            display: block;
            font-weight: 100;
            font-size: 80px;
            color: $accent-color;
            margin-bottom: 47px;
        }
    }
}

.block-about {
    display: flex;
    justify-content: space-between;
    margin-top: 82px;
    @media (max-width: 1020px) {
        flex-direction: column;
        max-width: 750px;
        margin: 82px auto 0;
    }

    h3 {
        margin-bottom: 18px;
        letter-spacing: 1.5px;
        color: $bg-dark-color;
    }

    &-text {
        flex-basis: calc(100% - 690px);

        &__desc {
            font-family: "Open Sans", serif;
            letter-spacing: 0.8px;
            color: $gray-text;
        }
    }

    &-social {
        display: flex;
        align-items: center;
        margin-top: 28px;

        &__title {
            font-family: "Roboto Slab", serif;
            font-size: 18px;
            color: $bg-dark-color;
            font-weight: 700;
            letter-spacing: 1.3px;
        }

        &__list {
            list-style-type: none;
            padding-left: 23px;

            & > li {
                display: inline-block;
                font-size: 18px;
                vertical-align: middle;
                margin-right: 12px;

                & > a {
                    color: $accent-color;
                }
            }
        }
    }

    &-table {
        flex-basis: 585px;
        @media (max-width: 1020px) {
            flex-basis: 100%;
            margin-top: 40px;
        }
        &__list {
            margin-top: 22px;
            padding: 0;
            counter-reset: list;
            column-count: 2;
            column-gap: 19px;

            & > li {
                position: relative;
                padding-left: 43px;
                color: $gray-text;
                font-family: "Open Sans", serif;
                counter-increment: list;
                list-style-type: none;
                margin-bottom: 8px;
                letter-spacing: 0.7px;

                &::before {
                    content: counter(list) ".";
                }

                &::after {
                    content: "+";
                    position: absolute;
                    top: calc(50% - 10px);
                    left: 6px;
                    padding: 0 6px;
                    line-height: 22px;
                    color: $light-color;
                    background-color: #5fc9b8;
                }
            }
        }
        &__list_next {
            counter-reset: list 10;
            position: relative;
            &::before {
                content: "";
                display: block;
                position: absolute;
                top: -16px;
                width: 100px;
                height: 2px;
                background-color: #5fc9b8;
            }
        }

        &__btn {
            background-color: $accent-color;
            color: #fff;
            margin-top: 12px;
            letter-spacing: 1.4px;
            padding: 16px 48px;
            border-radius: 3px;
        }
        h3 {
            color: $bg-dark-color;
        }

        h3 {
            margin-left: 4px;
        }
    }
}

.block-other-book {
    display: flex;
    margin-top: 85px;
    flex-direction: column;

    &__content {
        font-size: 48px;
        text-align: center;
        color: $bg-dark-color;
        letter-spacing: 2px;

        span {
            font-size: 18px;
            display: block;
            color: $accent-color;
            letter-spacing: 0.9px;
            padding-top: 23px;
        }
    }

    &__btn {
        display: block;
        max-width: 220px;
        margin: 84px auto 19px;
        padding: 12px 0;
        background-color: #65e4a6;
        color: #fff;
        text-align: center;
        border-radius: 3px;
        font-size: 18px;
        text-decoration: none;
        letter-spacing: 0.7px;
    }

    &-demo {
        display: flex;
        justify-content: space-around;
        margin-top: 85px;
        @media (max-width: 1020px) {
            flex-direction: column;
        }

        &-items {
            display: block;

            &__img {
                display: block;
                position: relative;
                width: 270px;
                height: 411px;
                margin: 0 auto 36px;
                background-image: url("../img/svg/littlebook.svg");
                background-repeat: no-repeat;

                &-title {
                    position: absolute;
                    top: 69px;
                    left: 81px;
                    color: #fff;
                    font-size: 24px;
                    text-transform: uppercase;
                    letter-spacing: 1.2px;

                    span {
                        display: block;
                        font-family: "Open Sans", serif;
                        font-size: 12px;
                        color: $accent-color;
                        text-transform: initial;
                        font-weight: 100;
                        letter-spacing: 0.5px;
                        padding-top: 10px;
                    }
                }

                &-author {
                    position: absolute;
                    bottom: 29px;
                    left: 80px;
                    padding: 0 auto;
                    font-family: "Open Sans", serif;
                    font-size: 12px;
                    color: $accent-color;
                    letter-spacing: 0.7px;
                }
            }

            &__desc {
                text-align: center;
                margin-bottom: 50px;

                & > h2 {
                    letter-spacing: 2px;
                    color: $bg-dark-color;
                }
                & > p {
                    font-family: "Open Sans", serif;
                    font-size: 15px;
                    letter-spacing: 0.75px;
                    margin-bottom: 17px;
                    max-width: 320px;
                    @media (max-width: 1020px) {
                        margin: 17px auto 0;
                    }
                }
                & > a {
                    text-decoration: none;
                    text-transform: uppercase;
                    font-family: "Open Sans", serif;
                    font-weight: 700;
                    color: $accent-color;

                    &:hover {
                        border-bottom: 3px solid $accent-color;
                    }
                }
            }
        }
    }
}

.block-blog {
    display: flex;
    justify-content: space-between;
    margin-top: 86px;
    @media (max-width: 900px) {
        flex-direction: column;
        max-width: 600px;
        margin: 85px auto 0;
    }
    &-item {
        flex-basis: 32%;
    }

    &-img {
        position: relative;
        width: 100%;
        height: 224px;
        overflow: hidden;
        margin-top: 34px;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 224px;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }

    h4 {
        font-size: 18px;
        margin-top: 20px;
        letter-spacing: 1px;
    }

    &-info {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        letter-spacing: 0.8px;
        margin-bottom: 12px;
        font-size: 15px;

        a {
            text-decoration: none;
            color: $accent-color;
        }
    }

    p {
        font-size: 15px;
        font-family: "Open Sans", serif;
    }
}

.block-blog__btn {
    display: block;
    max-width: 266px;
    margin: 85px auto 0;
    padding: 12px 0;
    background-color: #65e4a6;
    color: #fff;
    text-align: center;
    border-radius: 3px;
    font-size: 18px;
    text-decoration: none;
    letter-spacing: 0.7px;
}

.block-letter {
    display: flex;
    justify-content: space-between;
    @media (max-width: 900px) {
        flex-direction: column;
        text-align: center;
    }

    &__input {
        box-sizing: border-box;
        width: 100%;
        padding: 14px 0 15px 28px;
        background-color: #44444f;
        margin-top: 10px;
        font-family: "Open Sans", serif;
        @media (max-width: 900px) {
            max-width: 500px;
        }
    }

    &__btn {
        background-color: $accent-color;
        display: block;
        width: 100%;
        color: #fff;
        margin-top: 12px;
        letter-spacing: 1.4px;
        padding: 16px 0 16px 0;
        border-radius: 3px;
        font-family: "Open Sans", serif;
        @media (max-width: 900px) {
            max-width: 500px;
            margin: 12px auto 0;
        }
    }

    &__title {
        font-size: 48px;
        color: $bg-dark-color;
        letter-spacing: 1.9px;
        margin-top: 16px;
        margin-bottom: 24px;
    }
    &__text {
        font-family: "Open Sans", serif;
        letter-spacing: 0.3px;
        color: $gray-text;
        @media (max-width: 900px) {
            margin-bottom: 40px;
        }
    }

    &-item {
        flex-basis: 49%;
    }
}

.footer {
    background-color: $bg-dark-color;
    color: $gray-text;
}
.block-footer {
    display: flex;
    justify-content: space-between;
    padding: 160px 0 181px 0;
    &__logo {
        display: block;
        width: 138px;
        height: 28px;
        background-image: url("../img/svg/logofooter.svg");
        background-repeat: no-repeat;
    }
    &__desc {
        margin-top: 26px;
        margin-bottom: 24px;
        font-family: "Open Sans", serif;
        letter-spacing: 0.6px;
    }
    &__title {
        margin-top: 9px;
        font-size: 18px;
        color: $accent-color;
        text-transform: uppercase;
        letter-spacing: 0.8px;
    }
    &__list {
        list-style-type: none;
        margin-top: 18px;
        line-height: 28px;
        font-family: "Open Sans", serif;
        letter-spacing: 0.5px;
    }
    &__social {
        display: flex;
        list-style-type: none;
        &>li {
            margin-right: 10px;
            &>a {
                color: $accent-color;
            }
        }
    }
    &>div {

        &>span {
            font-family: "Open Sans", serif;
            letter-spacing: 0.6px;
        }
    }
    &-logo_left {
        flex-basis: 32%;
    }
    &-links {
    }
}
.block-slider {
    text-align: center;
    padding-top: 94px;
    @media (max-width: 1020px) {
        padding-top: 0;
    }
    h3 {
        color: $accent-color;
        margin-bottom: 5px;
        letter-spacing: 1px;
    }
    p {
        font-family: "Open Sans", serif;
        color: $gray-text;
        max-width: 760px;
        margin: 0 auto;
        line-height: 28px;
        letter-spacing: 0.75px;
    }
    &-expert {
        padding-bottom: 29px;
    }
    &-ava {
        display: block;
        position: relative;
        width: 152px;
        height: 152px;
        margin: 0 auto 27px;
    }
    &-bq {
        display: block;
        position: absolute;
        top: 0;
        right: -13px;
        width: 48px;
        height: 43px;
        background-image: url("../img/bq.png");
        background-repeat: no-repeat;
    }
    &-thumb {
        display: block;
        position: relative;
        width: 152px;
        height: 152px;
        overflow: hidden;
        margin: 0 auto 0;
        border-radius: 100%;
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 152px;
            height: 152px;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }
}
.slick-dots {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin-top: 34px;
    &>li {
        margin: 0 5px;
        &>button {
            text-indent: -9999px;
            width: 8px;
            height: 8px;
            border: none;
            background-color: $accent-color;
            border-radius: 100%;
            line-height: 0;
            font-size: 0;
            padding: 5px;
            cursor: pointer;
            &:focus {
                outline: none;
            }
        }
    }
    &>li.slick-active {
        &>button {
            width: 37px;
            border-radius: 10px;
        }
    }
}
